<template>
  <div id="dialog">
    <el-dialog title="搜索书籍" v-model="visible" width="30%" @close="onClose">
      <el-input placeholder="请输入内容" clearable v-model="input">
        <template #append>
          <el-button icon="el-icon-search"></el-button>
        </template>
      </el-input>
      <!-- <template #footer>
        <span class="dialog-footer">
          <el-button size="mini" @click="onClose">取 消</el-button>
          <el-button size="mini" type="primary" @click="onConfirm"
            >确 定</el-button
          >
        </span>
      </template> -->
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, reactive, watch, ref } from 'vue'
interface Props {
  dialogVisible: boolean;
}
interface State {
  visible: boolean;
}
export default defineComponent({
  name: 'Dialog',
  props: {
    dialogVisible: {
      type: Boolean,
      default: false
    }
  },
  setup (props, context) {
    const input = ref('')
    const { dialogVisible } = toRefs(props as Props)
    const parent = { ...context }
    const state = reactive<State>({
      visible: dialogVisible.value
    })
    watch(
      dialogVisible,
      newValue => {
        state.visible = newValue
      },
      { immediate: true }
    )
    const onClose = () => {
      parent.emit('update:dialogVisible', false)
    }
    const onConfirm = () => {
      parent.emit('on-confirm', '子组件的值')
    }
    return {
      ...toRefs(state),
      onClose,
      onConfirm,
      input
    }
  }
})
</script>

<style lang="scss">
@import '@/assets/css/variable.scss';
#dialog {
  .el-dialog {
    margin-top: 40vh !important;
    padding: 20px;
  }
  .el-dialog__headerbtn:focus .el-dialog__close,
  .el-dialog__headerbtn:hover .el-dialog__close {
    color: $color;
  }
  .el-input.is-active .el-input__inner,
  .el-input__inner:focus {
    border-color: $color;
  }
}
</style>
