<template>
  <div id="header" ref="headerRef">
    <el-row :gutter="15">
      <el-col :span="3">
        <div class="logo">
          <router-link to="/">
            <img src="../assets/logo_with_words.png" />
          </router-link>
        </div>
      </el-col>
      <el-col :span="15">
        <ul class="nav">
          <li class="nav-item"><router-link to="/">首页</router-link></li>
          <li class="nav-item"><router-link to="/books">图书</router-link></li>
          <li class="nav-item">
            <router-link to="/article">文章</router-link>
          </li>
        </ul>
      </el-col>
      <el-col :span="6">
        <ul class="sidebar_right">
          <li>
            <a
              href="#"
              class="search"
              @click.prevent="dialogVisible = true"
            ></a>
          </li>
          <li><router-link to="/cart" class="cart"></router-link></li>
          <li>
            <a href="#" class="user" @click.prevent="showLogin = true"></a>
          </li>
          <!-- <li>
            <el-popover
              placement="bottom"
              :width="200"
              trigger="click"
              popper-class="user_wrapper"
            >
              <template #reference>
                <a href="#" class="user" @click.prevent=""></a>
              </template>
              <div class="use_wrapper">
                <div class="user_image">
                  <el-avatar
                    src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"
                  ></el-avatar>
                </div>
                <div class="user_username">Li Ming</div>
                <div class="user_status">VIP</div>
              </div>
            </el-popover>
          </li> -->
        </ul>
      </el-col>
    </el-row>
    <dialogDevice
      v-model:dialogVisible="dialogVisible"
      @on-confirm="onConfirm"
    ></dialogDevice>
    <Login v-model:showLogin="showLogin" @on-confirm="onConfirm"></Login>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  onMounted,
  onUnmounted,
  ref,
  toRefs,
  reactive
} from 'vue'
import throttle from 'lodash/throttle'
import dialogDevice from '@/components/Dialog.vue'
import Login from '@/components/Login.vue'
interface State {
  dialogVisible: boolean;
  showLogin: boolean;
}
export default defineComponent({
  name: 'GlobalHeader',
  components: { dialogDevice, Login },
  setup (props, context) {
    const headerRef = ref<null | HTMLElement>(null)
    const windowScroll = throttle(
      () => {
        const { scrollTop } = document.documentElement
        if (scrollTop >= 180) {
          ;(headerRef as any).value.style.background = '#fff'
          ;(headerRef as any).value.style.boxShadow =
            '0 4px 8px 0 rgb(7 17 27 / 10%)'
        } else {
          ;(headerRef as any).value.style.background = 'rgba(0,0,0,0)'
          ;(headerRef as any).value.style.boxShadow = ''
        }
      },
      100,
      { trailing: false }
    )
    onMounted(() => {
      window.addEventListener('scroll', windowScroll)
    })
    onUnmounted(() => {
      window.removeEventListener('scroll', windowScroll)
    })
    const state = reactive<State>({
      dialogVisible: false,
      showLogin: false
    })
    const onConfirm = (value: string) => {
      console.log('父组件打印:', value)
      state.dialogVisible = false
    }
    return {
      ...toRefs(state),
      onConfirm,
      headerRef
    }
  }
})
</script>

<style lang="scss">
@import '@/assets/css/variable.scss';

.user_wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  .user_image {
    display: flex;
    justify-content: center;
  }
  .user_username {
    margin: 5px 0;
  }
  .user_status {
    color: $color;
    text-align: center;
  }
}

#header {
  padding: 0 80px;
  height: 90px;
  // 使 header 悬空（背景透明）
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 100;
  transition: background 0.5s;
  .logo {
    display: flex;
    height: 90px;
    line-height: 90px;
    img {
      vertical-align: middle;
      width: 100%;
    }
  }

  .nav {
    height: 90px;
    background: rgba(0, 0, 0, 0);

    display: flex;
    .nav-item {
      font-size: 18px;
      font-weight: 600;
      padding: 0 25px;
      a {
        height: 90px;
        align-items: center;
        display: flex;
        color: rgb(51, 51, 51);
      }
      a:hover {
        color: $color;
      }
    }
  }
  .sidebar_right {
    display: flex;
    height: 90px;
    justify-content: flex-end;

    li {
      padding: 0 15px;
      display: flex;
      height: 90px;

      a {
        align-items: center;
        display: flex;
        height: 100%;
        line-height: 35px;
        width: 24px;
      }
      .search {
        background: rgba(0, 0, 0, 0) url('../assets/icons/search.png') no-repeat
          scroll 0 center;
      }

      .cart {
        background: rgba(0, 0, 0, 0) url('../assets/icons/cart.png') no-repeat
          scroll 0 center;
      }
      .user {
        background: rgba(0, 0, 0, 0) url('../assets/icons/user.png') no-repeat
          scroll 0 center;
      }
    }
  }
}
</style>
