<template>
  <div id="Login">
    <el-dialog
      title=""
      v-model="visible"
      width="20%"
      @close="onClose"
      ref="elForm"
    >
      <div class="image_box">
        <img src="@/assets/logo_with_words.png" />
      </div>
      <el-tabs v-model="activeName">
        <!-- 登录页 -->
        <el-tab-pane label="登录" name="first">
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            class="demo-ruleForm"
          >
            <el-form-item label="" prop="name">
              <el-input
                v-model="ruleForm.username"
                placeholder="用户名/邮箱"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="name">
              <el-input
                v-model="ruleForm.password"
                placeholder="密码"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')"
                >登录</el-button
              >
              <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
          </el-form>
          <div>
            <a href="#" @click.prevent="showForget = true">忘记密码？</a>
          </div>
        </el-tab-pane>
        <el-tab-pane label="注册" name="second">
          <el-form
            :model="ruleForm"
            :rules="rules"
            ref="ruleForm"
            class="demo-ruleForm"
          >
            <el-form-item label="" prop="name">
              <el-input
                v-model="ruleForm.username"
                placeholder="用户名"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="name">
              <el-input
                v-model="ruleForm.username"
                placeholder="绑定邮箱"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="name">
              <el-input
                v-model="ruleForm.username"
                placeholder="密码"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="name">
              <el-input
                v-model="ruleForm.password"
                placeholder="请再次输入密码"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="name">
              <!-- <div style="display:flex">
                <el-input
                  v-model="ruleForm.username"
                  placeholder="验证码"
                ></el-input>
                <el-button type="primary">发送验证码</el-button>
              </div> -->

              <el-input v-model="ruleForm.username" placeholder="验证码">
                <template #append>
                  <el-button> 发送验证码</el-button>
                </template></el-input
              >
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')"
                >注册</el-button
              >
              <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
          </el-form>
          <div>
            <a href="#" @click.prevent="showForget = true">忘记密码？</a>
          </div>
        </el-tab-pane>
      </el-tabs>

      <!-- <template #footer>
        <span class="dialog-footer">
          <el-button size="mini" @click="onClose">取 消</el-button>
          <el-button size="mini" type="primary" @click="onConfirm"
            >确 定</el-button
          >
        </span>
      </template> -->
    </el-dialog>
    <Forget v-model:showForget="showForget" @on-confirm="onConfirm"></Forget>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, reactive, watch, ref } from 'vue'
import Forget from '@/components/Forget.vue'
interface Props {
  showLogin: boolean;
}
interface State {
  visible: boolean;
}
export default defineComponent({
  name: 'Login',
  props: {
    showLogin: {
      type: Boolean,
      default: false
    }
  },
  components: { Forget },
  setup (props, context) {
    const activeName = ref('first')
    const { showLogin } = toRefs(props as Props)
    const showForget = ref(false)
    const parent = { ...context }
    const state = reactive<State>({
      visible: showLogin.value
    })
    watch(
      showLogin,
      newValue => {
        state.visible = newValue
      },
      { immediate: true }
    )
    const onClose = () => {
      parent.emit('update:showLogin', false)
    }
    const onConfirm = () => {
      parent.emit('on-confirm', '子组件的值')
    }
    const ruleForm = { username: '', password: '' }
    const rules = {
      username: [
        { required: true, message: '请输入账号', trigger: 'blur' },
        { min: 8, max: 50, message: '长度在 8 到 50 个字符', trigger: 'blur' }
      ],
      password: [
        { required: true, message: '请输入密码', trigger: 'blur' },
        { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' }
      ]
    }
    const elForm = ref<HTMLElement | null>(null)
    const submitForm = (formName: any) => {
      ;(elForm as any).validate((valid: any) => {
        if (valid) {
          alert('submit!')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
    const resetForm = (formName: any) => {
      ;(elForm as any).resetFields()
    }

    return {
      ...toRefs(state),
      onClose,
      onConfirm,
      activeName,
      ruleForm,
      rules,
      submitForm,
      resetForm,
      showForget
    }
  }
})
</script>

<style lang="scss">
@import '@/assets/css/variable.scss';
#Login {
  .el-dialog {
    margin-top: 22vh !important;
    padding: 0 20px;
    .image_box {
      img {
        width: 80%;
        height: 80%；;
      }
    }
  }
  .el-dialog__headerbtn:focus .el-dialog__close,
  .el-dialog__headerbtn:hover .el-dialog__close {
    color: $color;
  }
}
</style>
