<template>
  <div id="Forget">
    <el-dialog
      title=""
      v-model="visible"
      width="20%"
      @close="onClose"
      ref="elForm"
    >
      <div class="forget_title">重置密码</div>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input
            v-model="ruleForm.username"
            placeholder="用户名/邮箱"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.username" placeholder="验证码">
            <template #append>
              <el-button>发送验证码</el-button>
            </template></el-input
          >
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.password" placeholder="密码"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">
            重置密码</el-button
          >
        </el-form-item>
      </el-form>
      <div>
        <a href="#" @click.prevent="onClose"
          ><i class="el-icon-back"></i> 返回登录</a
        >
      </div>
      <!-- <template #footer>
        <span class="dialog-footer">
          <el-button size="mini" @click="onClose">取 消</el-button>
          <el-button size="mini" type="primary" @click="onConfirm"
            >确 定</el-button
          >
        </span>
      </template> -->
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, reactive, watch, ref } from 'vue'
interface Props {
  showForget: boolean;
}
interface State {
  visible: boolean;
}
export default defineComponent({
  name: 'Forget',
  props: {
    showForget: {
      type: Boolean,
      default: false
    }
  },
  setup (props, context) {
    const { showForget } = toRefs(props as Props)
    const parent = { ...context }
    const state = reactive<State>({
      visible: showForget.value
    })
    const ruleForm = { username: '', password: '' }
    const rules = {
      username: [
        { required: true, message: '请输入账号', trigger: 'blur' },
        { min: 8, max: 50, message: '长度在 8 到 50 个字符', trigger: 'blur' }
      ],
      password: [
        { required: true, message: '请输入密码', trigger: 'blur' },
        { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' }
      ]
    }
    const elForm = ref<HTMLElement | null>(null)
    const submitForm = (formName: any) => {
      ;(elForm as any).validate((valid: any) => {
        if (valid) {
          alert('submit!')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
    const resetForm = (formName: any) => {
      ;(elForm as any).resetFields()
    }
    watch(
      showForget,
      newValue => {
        state.visible = newValue
      },
      { immediate: true }
    )
    const onClose = () => {
      parent.emit('update:showForget', false)
    }
    const onConfirm = () => {
      parent.emit('on-confirm', '子组件的值')
    }
    return {
      ...toRefs(state),
      onClose,
      onConfirm,
      ruleForm,
      rules,
      submitForm,
      resetForm
    }
  }
})
</script>

<style lang="scss">
@import '@/assets/css/variable.scss';
#Forget {
  .el-dialog {
    margin-top: 25vh !important;
    padding: 20px;
  }
  .el-dialog__body {
    padding: 0 20px 30px 20px;
  }
  .forget_title {
    font-size: 26px;
    font-weight: 700;
    margin: 28px 0;
  }

  .el-dialog__headerbtn:focus .el-dialog__close,
  .el-dialog__headerbtn:hover .el-dialog__close {
    color: $color;
  }
  .el-input.is-active .el-input__inner,
  .el-input__inner:focus {
    border-color: $color;
  }
}
</style>
