
import { defineComponent, toRefs, reactive, watch, ref } from 'vue'
import Forget from '@/components/Forget.vue'
interface Props {
  showLogin: boolean;
}
interface State {
  visible: boolean;
}
export default defineComponent({
  name: 'Login',
  props: {
    showLogin: {
      type: Boolean,
      default: false
    }
  },
  components: { Forget },
  setup (props, context) {
    const activeName = ref('first')
    const { showLogin } = toRefs(props as Props)
    const showForget = ref(false)
    const parent = { ...context }
    const state = reactive<State>({
      visible: showLogin.value
    })
    watch(
      showLogin,
      newValue => {
        state.visible = newValue
      },
      { immediate: true }
    )
    const onClose = () => {
      parent.emit('update:showLogin', false)
    }
    const onConfirm = () => {
      parent.emit('on-confirm', '子组件的值')
    }
    const ruleForm = { username: '', password: '' }
    const rules = {
      username: [
        { required: true, message: '请输入账号', trigger: 'blur' },
        { min: 8, max: 50, message: '长度在 8 到 50 个字符', trigger: 'blur' }
      ],
      password: [
        { required: true, message: '请输入密码', trigger: 'blur' },
        { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' }
      ]
    }
    const elForm = ref<HTMLElement | null>(null)
    const submitForm = (formName: any) => {
      ;(elForm as any).validate((valid: any) => {
        if (valid) {
          alert('submit!')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
    const resetForm = (formName: any) => {
      ;(elForm as any).resetFields()
    }

    return {
      ...toRefs(state),
      onClose,
      onConfirm,
      activeName,
      ruleForm,
      rules,
      submitForm,
      resetForm,
      showForget
    }
  }
})
